import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule, Routes} from "@angular/router";

import { FormsModule } from '@angular/forms' ;
import { AppComponent } from './app.component';
import { NavBarComponent } from './components/nav-bar/nav-bar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { AdminComponent } from './admin/admin.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { HomeComponent } from './components/home/home.component';

import { ValidateService } from "./services/validate.service";
import { AuthService} from "./services/auth.service";
import { AlertsModule} from "angular-alert-module";
import {HttpClient, HttpClientModule} from "@angular/common/http";
import { ProfileComponent } from './components/profile/profile.component';
import { AuthGuard } from "./guards/auth.guard";
import { AddnewlabComponent } from './components/addnewlab/addnewlab.component';
import { MakereservationComponent } from './components/makereservation/makereservation.component';
import { MyreservationComponent } from './components/myreservation/myreservation.component';
import { SideBarComponent } from './components/side-bar/side-bar.component';
import { ReportsComponent } from './reports/reports.component';
import { AdduserComponent } from './components/adduser/adduser.component';
import { AdmindashboadComponent } from './components/admindashboad/admindashboad.component';


const appRoutes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'admin', component: AdminComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate:[AuthGuard] },
  {path: 'admindashboard', component: AdmindashboadComponent, canActivate:[AuthGuard] },
  {path: 'profile', component: ProfileComponent, canActivate:[AuthGuard] },
  {path: 'addlab', component: AddnewlabComponent, canActivate:[AuthGuard] },
  {path: 'makereservation', component: MakereservationComponent, canActivate:[AuthGuard] },
  {path: 'checkavailability', component: MyreservationComponent, canActivate:[AuthGuard] },
  {path: 'adduser', component: AdduserComponent, canActivate:[AuthGuard] },
  {path: 'reports', component:ReportsComponent , canActivate:[AuthGuard] }

]

@NgModule({
  declarations: [
    AppComponent,
    NavBarComponent,
    LoginComponent,
    RegisterComponent,
    AdminComponent,
    DashboardComponent,
    HomeComponent,
    ProfileComponent,
    AddnewlabComponent,
    MakereservationComponent,
    MyreservationComponent,
    SideBarComponent,
    ReportsComponent,
    AdduserComponent,
    AdmindashboadComponent,
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    FormsModule,
    AlertsModule.forRoot(),
    HttpClientModule
  ],
  providers: [ValidateService, AuthService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
