import { Component, OnInit } from '@angular/core';
import { AuthService} from "../../services/auth.service";
import { Router } from "@angular/router";
import {ValidateService} from "../../services/validate.service";
import {AlertsService} from "angular-alert-module"
import {FormControl} from "@angular/forms";


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  user: Object;

  name: String ;
  email: String ;
  username: String;
  type: String;
  password: String;
  confirmPassword: String;

  btnText:String= "blaaa";

  typeValue: String;
  nameValue: String ;
  emailValue: String ;
  passwordValue: String;
  confirmPasswordValue: String;

  constructor(
    private validateService: ValidateService,
    private alerts: AlertsService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
    this.authService.getProfile().subscribe(profile => {
      this.user = profile['user'];
      this.nameValue = this.user['name'];
      this.emailValue = this.user['email'];
      this.typeValue = this.user['type'];
      this.nameControl.setValue(this.user['name']);
      console.log(this.nameValue,this.emailValue);
    },
    err =>{
      console.log(err);
      return false;
    });
  }
  nameControl= new FormControl('',[]);

  onRegisterSubmit() {
    const user = {
      name: this.name,
      email: this.email,
      // username: this.username,
      //type:this.type,
      password: this.password,
      confirmPassword: this.confirmPassword
    }

    if (!this.validateService.validateRegister(user)) {
      this.alerts.setMessage('please fill the all fields', 'error');
      return false;
    }

    if (!this.validateService.validatPassword(user)) {
      this.alerts.setMessage('password mismatch', 'error');
      this.confirmPasswordValue = null;
      this.passwordValue = null;
      return false;
    }

    if (!this.validateService.validateEmail(user.email)) {
      this.alerts.setMessage('please use a valid email', 'error');
      this.emailValue = null;
      return false;
    }

    //   this.authService.updateUser(user).subscribe(data =>{
    //     if (data['success']){
    //       this.alerts.setMessage('Successfully Registered', 'success');
    //       this.router.navigate(['/login']);
    //     }else{
    //       this.alerts.setMessage( data['msg'], 'error');
    //       this.router.navigate(['/register']);
    //     }
    //   });
    //
    // }

  }
}
