import { Component, OnInit } from '@angular/core';
import {AlertsService} from "angular-alert-module";
import {ValidateService} from "../../services/validate.service";
import {AuthService} from "../../services/auth.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-addnewlab',
  templateUrl: './addnewlab.component.html',
  styleUrls: ['./addnewlab.component.css']
})
export class AddnewlabComponent implements OnInit {
  labName: String;
  labDescription: String;
  constructor(
    private validateService: ValidateService,
    private alerts: AlertsService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {

  }

  addNewLab(){
    const lab ={
      labName: this.labName,
      labDescription: this.labDescription
    }

    if(!this.validateService.validaLab(lab)){
      this.alerts.setMessage('please fill the all fields', 'error');
      return false;
    }

    this.authService.registerLab(lab).subscribe(data =>{
      if (data['success']){
        this.alerts.setMessage('Successfully Registered', 'success');
        this.router.navigate(['/dashboard']);
      }else{
        this.alerts.setMessage('Somethings went wrong ', 'error');
        this.router.navigate(['/dashboard']);
      }
    });
  }


}
