import { Component, OnInit } from '@angular/core';
import { AuthService} from "../../services/auth.service";
import { Router} from "@angular/router";
import { AlertsService} from "angular-alert-module";
import { ValidateService} from "../../services/validate.service";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  email: String;
  password: String;

  emailValue: String;
  passwordValue: String;


  constructor(
    private aurthService:AuthService,
    private alerts: AlertsService,
    private router: Router,
    private validateService: ValidateService
  ) { }

  ngOnInit() {
  }

  onLoginSubmit(){
    const user = {
      email: this.email,
      password: this.password
    }

    if(!this.validateService.validateLogin(user)){
      this.alerts.setMessage('please insert all data', 'error');
      this.passwordValue = null;
      return false;
    }

    // have to implement
    this.aurthService.authenticateUser(user).subscribe(data => {
      if (data['success']){
        this.aurthService.storeUserData(data['token'], data['user']);
        this.alerts.setMessage('You are logged in', 'success');
        this.router.navigate(['dashboard']);
      }else{
        console.log(data);
        this.alerts.setMessage(data['msg'], 'error');
        this.router.navigate(['login']);
      }
    });
  }

}
