import { Component, OnInit } from '@angular/core';
import {AuthService} from "../../services/auth.service";
import {ValidateService} from "../../services/validate.service";
import {AlertsService} from "angular-alert-module";
import {Router} from "@angular/router";

@Component({
  selector: 'app-admindashboad',
  templateUrl: './admindashboad.component.html',
  styleUrls: ['./admindashboad.component.css']
})
export class AdmindashboadComponent implements OnInit {

  reservations = [];
  name:String;
  _id:String;
  labName: String;
  date:String;
  timePeriod: String;
  reservedBy: String ;
  today:Date;
  constructor(
    private validateService: ValidateService,
    private alerts: AlertsService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
    this.authService.getAllReservations().subscribe(labReservation => {
        this.reservations = (labReservation['labReservation']);
      },
      err => {
        console.log(err);
        return false;
      });
  }
  delete(_id){
    this._id = _id;
    const labReservation ={
      _id:this._id,
      labName: "bla",
      reserveDate: "bla",
      startTime: 'bla',
      endTime: 'bla',
      reservedBy: 'bla',
      timePeriod: "bla"
    }
    this.authService.deleteReservation(labReservation).subscribe(data =>{

      if (data['success']){
        this.alerts.setMessage('Successfully Delete the Reservation', 'success');
        this.router.navigate(['/admindashboard']);
        window.location.reload();
      }else{
        this.alerts.setMessage( data['msg'],'error');
        this.router.navigate(['/admindashboard']);
      }
    });

  }

}
