import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { HttpClient,HttpHeaders } from "@angular/common/http";
import { JwtHelperService } from '@auth0/angular-jwt';
import {t} from "@angular/core/src/render3";

const helper = new JwtHelperService();

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  authToken: any;
  user: any;

  constructor(
    private http: HttpClient
    ) { }

  registerUser(user){
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/users/register', user,{headers: headers})
      .pipe(map(res => res));
  }


  registerLab(lab){
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/labs/registerlab', lab,{headers: headers})
      .pipe(map(res => res));
  }

  authenticateUser(user){
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/users/authenticate', user,{headers: headers})
      .pipe(map(res => res));
  }

  makeReservation(labReservation){
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/labreservations/makeReservation', labReservation,{headers: headers})
      .pipe(map(res => res));
  }

  deleteReservation(labReservation){
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/labreservations/deletereservation', labReservation,{headers: headers})
      .pipe(map(res => res));
  }

  checkReservations(labReservation){
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/labreservations/checkreservations', labReservation, {headers: headers})
      .pipe(map(res => res));
  }



  getProfile(){
    this.loadToken();
    let headers = new HttpHeaders({
      'Authorization': this.authToken,
      'Content-Type': 'application/json'
    });
    return this.http.get('http://localhost:3000/users/profile',{headers: headers})
      .pipe(map(res => res));
  }

  getAllReservations(){
    return this.http.get('http://localhost:3000/labreservations/allreservations')
      .pipe(map(res => res));
  }

  storeUserData(token, user){
    localStorage.setItem('id_token',token);
    localStorage.setItem('user',JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loadToken(){
    this.authToken = localStorage.getItem('id_token');
  }

  loggedIn(){
    const isExpired = helper.isTokenExpired(localStorage.getItem('id_token'));
    return !isExpired;
  }

  getType(){
    const items = localStorage.getItem('user').split('"');
    const type = items[11];
   //console.log(items[11])
    if(type != 'admin'){
      return false;
    }else {
      return true;
    }

  }

  getUserName(){
    const items = localStorage.getItem('user').split('"');
    const user = items[7];
    return user;
    //full name
  }

  getUser(){
    const items = localStorage.getItem('user').split('"');
    const user = items[15];
    return user;
    //username
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
}
