import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {AuthService} from "../../services/auth.service";
import {ValidateService} from "../../services/validate.service";
import {AlertsService} from "angular-alert-module";

@Component({
  selector: 'app-makereservation',
  templateUrl: './makereservation.component.html',
  styleUrls: ['./makereservation.component.css']
})
export class MakereservationComponent implements OnInit {

  labName: String;
  date:String;
  timePeriod: String;
  startTime: String;
  endTime: String;
  reservedBy: String;
  today: number;

  constructor(
    private authService:AuthService,
    private alerts: AlertsService,
    private router: Router,
    private validateService: ValidateService
  ) { }

  ngOnInit() {
    this.today = Date.now();
    this.reservedBy = this.authService.getUserName();
  }

  setTime(timePeriod){
    if(timePeriod == "1"){
      this.startTime = "08:00";
      this.endTime = "09:00";
    }if(timePeriod == "2"){
      this.startTime = "09:00";
      this.endTime = "10:00";
    }if(timePeriod == "3"){
      this.startTime = "10:00";
      this.endTime = "11:00";
    }if(timePeriod == "4"){
      this.startTime = "11:00";
      this.endTime = "12:00";
    }if(timePeriod == "5"){
      this.startTime = "12:00";
      this.endTime = "13:00";
    }if(timePeriod == "6"){
      this.startTime = "13:00";
      this.endTime = "14:00";
    }if(timePeriod == "7"){
      this.startTime = "14:00";
      this.endTime = "15:00";
    }if(timePeriod == "8"){
      this.startTime = "15:00";
      this.endTime = "16:00";
    }if(timePeriod == "9"){
      this.startTime = "16:00";
      this.endTime = "17:00";
    }
  }

  makeReservation(){
    this.setTime(this.timePeriod);
    const labReservation ={
      labName:this.labName,
      reserveDate: this.date,
      startTime: this.startTime,
      endTime: this.endTime,
      reservedBy: this.reservedBy,
      timePeriod: this.timePeriod
    }


    if(!this.validateService.validateReservations(labReservation)){
      this.alerts.setMessage('please fill the all fields', 'error');
      return false;
    }

    this.authService.makeReservation(labReservation).subscribe(data =>{

      if (data['success']){
        this.alerts.setMessage('Successfully Made the Reservation', 'success');
        this.router.navigate(['/dashboard']);
      }else{
        this.alerts.setMessage( data['msg'],'error');
        this.router.navigate(['/makereservation']);
      }
    });

  }



}
