import { Component, OnInit } from '@angular/core';
import {AlertsService} from "angular-alert-module";
import {Router} from "@angular/router";
import {AuthService} from "../../services/auth.service";
import {ValidateService} from "../../services/validate.service";
import {forEach} from "@angular/router/src/utils/collection";

@Component({
  selector: 'app-myreservation',
  templateUrl: './myreservation.component.html',
  styleUrls: ['./myreservation.component.css']
})
export class MyreservationComponent implements OnInit {

  labName: String;
  date:String;
  timePeriod: String;
  reservedBy: String = this.authService.getUserName();
  today:number;

  reservations = [];
  labs = ["Lab A", "Lab B", "Lab C", "Lab D", "Lab E" , "Third Year Lab", "Electronic Lab"];
  status = [];
  constructor(
    private authService:AuthService,
    private alerts: AlertsService,
    private router: Router,
    private validateService: ValidateService
  ) { }

  ngOnInit() {
    this.today = Date.now();
  }

  CheckReservation(){
    const labReservation ={
      labName: "bla",
      reserveDate: this.date,
      startTime: 'bla',
      endTime: 'bla',
      reservedBy: 'bla',
      timePeriod: this.timePeriod
    }

    if(!this.validateService.validateReservations(labReservation)){
      this.alerts.setMessage('please fill the all fields', 'error');
      return false;
    }

    this.authService.checkReservations(labReservation).subscribe(labReservation =>{
        this.reservations = (labReservation['labReservation']);
        if (this.reservations == []){
          for(var x=0; x <this.labs.length - 1; x++){
            this.status[x] = "Available";
          }
        }else{
          for(var x=0; x <this.labs.length; x++){
            this.status[x] = {name:this.labs[x],state:"Available"};
            for (var i=0; i<this.reservations.length; i++) {
              if (this.labs[x] == this.reservations[i]){
                this.status[x] = {name:this.labs[x],state:"Unavailable"};
              }
            }
          }
        }

      },
      err => {
        console.log(err);
        return false;
     });
  }

}
